export default {
  icon: 'users',
  title: '',
  name: 'users',
  auth: true,
  component: resolve => require(['views/users'], resolve),
  subRoutes: {
    '/': {
      name: 'users/all',
      auth: true,
      hidden: true,
      component: resolve => require(['views/users/all'], resolve)
    },
    '/:id': {
      name: 'users/detail',
      auth: true,
      hidden: true,
      component: resolve => require(['views/users/detail'], resolve)
    }
  }
}

