export default {
  icon: 'user',
  title: '',
  name: 'user',
  auth: true,
  component: resolve => require(['views/user'], resolve),
  subRoutes: {
    '/': {
      name: 'user/profile',
      auth: true,
      hidden: true,
      component: resolve => require(['views/user/profile'], resolve)
    },
    '/logout': {
      icon: 'logout',
      title: '',
      name: 'user/logout',
      auth: true,
      component: resolve => require(['views/user/logout'], resolve)
    }
  }
}



