export default {
  icon: 'pages',
  title: '',
  name: 'pages',
  auth: true,
  component: resolve => require(['views/pages'], resolve),
  subRoutes: {
    '/': {
      name: 'pages/all',
      auth: true,
      hidden: true,
      component: resolve => require(['views/pages/all'], resolve)
    },
    '/create': {
      icon: 'create',
      title: '',
      name: 'pages/create',
      auth: true,
      component: resolve => require(['views/pages/create'], resolve)
    }
  }
}



