export default {
  icon: 'roles',
  title: '',
  name: 'roles',
  auth: true,
  component: resolve => require(['views/roles'], resolve),
  subRoutes: {
    '/': {
      name: 'roles/all',
      auth: true,
      hidden: true,
      component: resolve => require(['views/roles/all'], resolve)
    },
    '/create': {
      icon: 'create',
      title: '',
      name: 'roles/create',
      auth: true,
      component: resolve => require(['views/roles/create'], resolve)
    }
  }
}



